import React from 'react'
import Scramble from 'react-scramble'
import "./ScrambleText.css"

class ScrambleText extends React.Component {
    render() {

        let text;
        let text1 = "Заказать Проект?"
        let text2 = "Сотрудничать?"
        let text3 = "Устроиться?"
        let text4 = "Связаться?"
        if(window.location.toString().indexOf('partners')>0){
            text = text2
        } else if(window.location.toString().indexOf('vacancies')>0){
            text = text3
        }else if(window.location.toString().indexOf('contacts')>0){
            text = text4
        } else{
            text = text1
        }

        return (
            <div className="ScrambleText d-flex justify-content-center">
                <a href="/request">
                    <Scramble
                        autoStart
                        mouseEnterTrigger="restart"
                        text={text}
                        steps={[
                            {
                                roll: 30,
                                action: '+',
                                type: 'all',
                            },
                            {
                                action: '-',
                                type: 'forward',
                            },
                        ]}
                    />
                </a>
            </div>
        )
    }
}

export default ScrambleText