import React from "react";
import {Link, NavLink} from "react-router-dom"
import './Header.css'

const Header = () =>{

    const toggleClass = () =>{
        document.getElementById("headerMenu").classList.toggle("d-none")
    }

    return(
        <div className="Header d-flex justify-content-between">
            <div className="Header__logo d-md-flex align-content-end">
                <Link to='/'><img src={require("../img/logo.png")} alt="Wocom"/></Link>
            </div>
            <div onClick={toggleClass} id="headerMenu" className="Header__menu d-lg-flex d-none">
                <ul className="d-lg-flex flex-column flex-lg-row">
                    <li>
                        <NavLink to="/">О нас</NavLink>
                    </li>
                    <li>
                        <NavLink to="/projects">Проекты</NavLink>
                    </li>
                    <li>
                        <NavLink to="/services">Услуги</NavLink>
                    </li>
                    <li>
                        <NavLink to="/partners">Партнерам</NavLink>
                    </li>
                    <li>
                        <NavLink to="/vacancies">Вакансии</NavLink>
                    </li>
                    <li>
                        <NavLink to="/contacts">Контакты</NavLink>
                    </li>
                    <li>
                        <NavLink to="/request">Оставить заявку</NavLink>
                    </li>
                </ul>
            </div>
            <div onClick={toggleClass} className="Header__toggle d-block d-lg-none">≡</div>
        </div>
    )
}

export default Header