import React,{useState} from "react";
import "./Request.css"

const Request = () =>{
    const [name, setName] = useState('');
    const [phone, setPhone] = useState('');
    const [description, setDescription] = useState('');
    const [file, setFile] = useState('');

    const handleForm = () =>{

        let formData = [name, phone, description, file]

        fetch('//example.api/').then((formData) => formData.json());
        alert("Заявка успешно отправлена")

    }

    return(
        <div className="Request container">
            <div className="Projects__title d-flex justify-content-center">Оставить заявку</div>
            <div className="Request__form col-12 d-flex justify-content-center">
                <form className="col-12 col-md-6" onSubmit={handleForm}>
                    <label >Имя</label><br/>
                    <input type="text" name="name" id="name" required onChange={e => setName(e.target.value)}/><br/>
                    <label>Номер телефона</label><br/>
                    <input type="text" name="phone" required  onChange={e => setPhone(e.target.value)}/><br/>
                    <label>Описание проекта</label><br/>
                    <textarea value={description} onChange={e => setDescription(e.target.value)}> </textarea><br/>
                    <label htmlFor="file" className="label--last" >Прикрепить файл <img className="clip" alt="" src="https://image.flaticon.com/icons/png/512/124/124506.png"/></label><br/>
                    <input type="file" name="file" id="file" onChange={e => setFile(e.target.value)}/><br/>
                    <button className="Request__button" type="submit">Отправить</button>
                </form>
            </div>
        </div>
    )
}

export default Request