import React from "react";
import "./Home.css"
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';


const Home = () =>{

    return(
    <div className="Home d-flex flex-column container">
        <h1>Creative IT studio</h1>
        <div className="Home__banner container">
            <OwlCarousel
                className="owl-theme"
                items={1}
                loop
                autoplay
            >
                <div className="item Home__owl"><img src="https://cs11.pikabu.ru/post_img/big/2018/06/19/6/1529402285160890095.jpg" alt=""/></div>
                <div className="item Home__owl"><img src="https://klike.net/uploads/posts/2019-06/1561182204_1.jpg" alt=""/></div>
                <div className="item Home__owl"><img src="https://img1.liveinternet.ru/images/attach/b/3/23/73/23073843_mzgsirds193.jpg" alt=""/></div>
            </OwlCarousel>
        </div>
    </div>
    )
}

export default Home
