import React from "react";
import "./Services.css"
import {Link} from "react-router-dom";

const Services = () =>{

    const randomColor = () =>{
        let colorRandom = Math.floor(Math.random()*4 + 1)
        document.getElementById("Services__all").classList.toggle(`Project__all${colorRandom}`)
    }

    return(
        <div>
            <div className="Services d-flex col-12 flex-column flex-lg-row">
                <div className="Services__left col-12 col-lg-6">
                    <div className="Services__title">Услуги</div>
                    <div className="Services__item d-flex"><span className="Services__number">01 </span>Создание сайтов</div>
                    <div className="Services__item d-flex"><span className="Services__number">02 </span>Дизайн</div>
                    <div className="Services__item d-flex"><span className="Services__number">03 </span>Mobile App</div>
                </div>
                <div className="Services__right col-lg-5">
                    <p className="Services__slogan">Опыт работы помогает нам находить лучшие решения в каждом конкретном случае, любой проект для нас — это возможность повысить собственный уровень.</p>
                </div>
            </div>
            <div className="d-flex justify-content-center Project__all" id="Services__all" onMouseOver={randomColor}><Link to="/services">Подробнее</Link></div>
        </div>

    )
}

export default Services