import React from "react";
import "./Project.css"
import {Link} from "react-router-dom";

const Project = () =>{

    const ProjectHover = (e) =>{
        Object.values(document.getElementsByClassName("Project__image")).map(i => {
            i.classList.remove("img-active")
        })
        document.getElementById(`project_${e}`).classList.add("img-active")
    }

    const randomColor = () =>{
        let colorRandom = Math.floor(Math.random()*4 + 1)
        document.getElementById("Project__all").classList.toggle(`Project__all${colorRandom}`)
    }


    return(
        <div>
            <div className="Project d-flex flex-column flex-lg-row justify-content-around">
                <div className="col-lg-6 col-12 Project__left">
                    <p className="Project__slogan">Since 2016</p>
                    <img id="project_1" className="Project__image img-active" src={require("../img/project/project_99.jpg")} alt="icon"/>
                    <img id="project_2" className="Project__image" src={require("../img/project/project_bars.jpg")} alt="icon"/>
                    <img id="project_3" className="Project__image" src={require("../img/project/project_rsi.jpg")} alt="icon"/>
                    <img id="project_4" className="Project__image" src={require("../img/project/project_kpf.jpg")} alt="icon"/>
                    <img id="project_5" className="Project__image" src={require("../img/project/project_2020.jpg")} alt="icon"/>
                </div>
                <div className="col-lg-5 col-12 Project__link">
                    <ul>
                        <li>
                            <Link onMouseOver={e => ProjectHover(1)} to="/projects/p?id1">
                                Школа для подготовки к ЕГЭ <br/>
                                <p className="Project__description">Front-end, Back-end</p>
                            </Link>
                        </li>
                        <li>
                            <Link onMouseOver={e => ProjectHover(2)} to="/projects/p?id2">
                                Учебный центр<br/>
                                <p className="Project__description">Корпоративный сайт сети, Back-end</p>
                            </Link>
                        </li>
                        <li>
                            <Link onMouseOver={e => ProjectHover(3)} to="/projects/p?id3">
                                Фондовый сервис <br/>
                                <p className="Project__description">Front-end, Back-end</p>
                            </Link>
                        </li>
                        <li>
                            <Link onMouseOver={e => ProjectHover(4)} to="/projects/p?id4">
                                Юридическая организация <br/>
                                <p className="Project__description">Корпоративный сайт сети, Front-end, Back-end</p>
                            </Link>
                        </li>
                        <li>
                            <Link onMouseOver={e => ProjectHover(5)} to="/projects/p?id5">
                                Торговая площадка <br/>
                                <p className="Project__description">Front-end, Back-end</p>
                            </Link>
                        </li>
                    </ul>
                </div>
            </div>
            <div className="d-flex justify-content-center Project__all" id="Project__all" onMouseOver={randomColor}><Link to="/projects">Посмотреть все</Link></div>
        </div>
    )
}

export default Project