import React from "react";
import "./Vacancies.css"
import ScrambleText from "../../components/scramble/ScrambleText";

const Vacancies = () =>{


    let countClick = 0

    const vacanciesActive = (e) =>{
        countClick++
        document.getElementById(`vd${e}`).classList.toggle("d-none")
        if(countClick % 2 !== 0){
            document.getElementById(`vn${e}`).innerText="-"
        } else{
            document.getElementById(`vn${e}`).innerText="+"
        }
    }


    return(
        <div className="container Vacancies">
            <div className="Projects__title d-flex justify-content-center">Наши вакансии</div>
            <div className="Vacancies__subtitle">Мы находимся в поиске лучших специалистов</div>
            <div className="d-flex flex-column">
                <div className="d-flex justify-content-between Vacancies__item" onClick={e => vacanciesActive(1)}>
                    <div className="Vacancies__name">Дизайнер</div>
                    <div id="vn1" className="Vacancies__name">+</div>
                </div>
                <div id="vd1" className="Vacancies__description d-none">
                    <p>
                        Требуемый опыт работы: 1–3 года<br/>
                        Полная занятость, полный день<br/>
                        <b>Обязанности:</b><br/>
                        Дизайн сайтов.<br/>
                        Создание различных графических материалов: рекламной и сувенирной продукции, макетов инфографики, презентаций и др.<br/>
                        <b>Требования:</b><br/>
                        Наличие портфолио.<br/>
                        Владение Adobe Photoshop‚ Adobe Illustrator.<br/>
                        Работа с векторной и растровой графикой.<br/>
                        Понимание принципов usability, знание современных трендов и технологий в digital дизайне.<br/>
                        Внимание к деталям‚ инициативность‚ активность‚ ответственность, ориентированность на результат.<br/>
                        ВАЖНО! Резюме без портфолио не рассматриваются.
                    </p>
                </div>


                <div className="d-flex justify-content-between Vacancies__item" onClick={e => vacanciesActive(2)}>
                    <div className="Vacancies__name">PHP guru</div>
                    <div id="vn2"  className="Vacancies__name">+</div>
                </div>
                <div id="vd2" className="Vacancies__description d-none">
                    <p>
                        Требуемый опыт работы: 1–3 года<br/>
                        Полная занятость, полный день<br/>
                        <b>Обязанности:</b><br/>
                        текст<br/>
                        текст<br/>
                        <b>Требования:</b><br/>
                        Наличие портфолио.<br/>
                        текст<br/>
                        текст<br/>
                        текст<br/>
                        текст<br/>
                        текст
                    </p>
                </div>


                <div className="d-flex justify-content-between Vacancies__item" onClick={e => vacanciesActive(3)}>
                    <div className="Vacancies__name">JavaScript ninja</div>
                    <div id="vn3"  className="Vacancies__name">+</div>
                </div>
                <div id="vd3" className="Vacancies__description d-none">
                    <p>
                        Требуемый опыт работы: 1–3 года<br/>
                        Полная занятость, полный день<br/>
                        <b>Обязанности:</b><br/>
                        текст<br/>
                        текст<br/>
                        <b>Требования:</b><br/>
                        Наличие портфолио.<br/>
                        текст<br/>
                        текст<br/>
                        текст<br/>
                        текст<br/>
                        текст
                    </p>
                </div>

            </div>
            <ScrambleText/>
        </div>
    )
}

export default Vacancies