import React from 'react';
import {BrowserRouter, Route} from "react-router-dom";
import Header from "./components/header/Header";
import Footer from "./components/footer/Footer";
import Intro from "./components/intro/Intro";
import Projects from "./sections/projects/Projects";
import Sentence from "./sections/sentence/Sentence";
import Vacancies from "./sections/vacancies/Vacancies";
import Contacts from "./sections/contacts/Contacts";
import ProjectItem from "./sections/projectitem/ProjectItem";
import Partners from "./sections/partners/Partners";
import Request from "./sections/request/Request";

const App = () =>{


  return (
    <div className="App">
        <BrowserRouter>
            <Header/>
            <Route exact path='/' component={Intro}/>
            <Route exact path='/projects' component={Projects}/>
            <Route exact path='/projects/:slug' component={ProjectItem}/>
            <Route path='/services' component={Sentence}/>
            <Route path='/partners' component={Partners}/>
            <Route path='/vacancies' component={Vacancies}/>
            <Route path='/contacts' component={Contacts}/>
            <Route path='/request' component={Request}/>
            <Footer/>
        </BrowserRouter>
    </div>
  )
}

export default App;
