import React from "react";
import {Link} from "react-router-dom";
import "./Footer.css"

const Footer = () =>{

    return(
        <footer>
            <div className="d-flex flex-sm-row flex-column-reverse Footer justify-content-between">
                <div className="Footer__left d-flex flex-column ">
                    <a href="mailto:info@wocom.biz">E-Mail: info@wocom.biz</a>
                    <a href="tel:+7 967 370-33-35">+7 967 370-33-35</a>
                    <p>Wocom © 2020</p>
                </div>
                <div className="Footer__center flex-column flex-lg-row d-none d-sm-flex">
                    <Link to="/">Компания</Link>
                    <Link to="/vacancies">Вакансии</Link>
                    <Link to="/contacts">Контакты</Link>
                </div>
                <div className="Footer__right">
                    <Link to="/"><img className="Footer__social"  alt="" src={require("../img/instagram.png")}/></Link>
                    <Link to="/"><img className="Footer__social"  alt="" src={require("../img/behance.png")}/></Link>
                    <Link to="/"><img className="Footer__social"  alt="" src={require("../img/linkedin.png")}/></Link>
                </div>
            </div>
        </footer>
    )
}

export default Footer